
import React, { useEffect, useState, useRef } from "react";
import { getStore, gen16CodeId, ObjectKeys, callDispatchEvent, fetchPostH, getUrlServerSide, updStore, filterArrAccounts } from "../../../stores/Utils";

import {DropInput_Cmpt } from "../../../components";
import "./styles.css"


const DropInput = DropInput_Cmpt();





const SelectDropAccount  = ({ handleClick,placeholder }) => {

	
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [loading, set_loading] = useState(false);
	const [list, setList] = useState([]);

	const searchRef = useRef();

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
    });


    

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}

	const handle_search =  (v) => {
		setList(filterArrAccounts(getStore("account_list"),v))
		loading && set_loading(false)
	}

	const handle_empty =  (v) => {
		setList([]);
		loading && set_loading(false)
	}
	
	
    
    const selectItem =  (acc,subacc) => {
		handleClick(acc,subacc);
		searchRef.current.clearTx();
	}

	

    return (
		<div>
			<DropInput
				placeholder={placeholder ||"buscar una cuenta"} 
				width={420}
				fetchData={handle_search}
				loading={loading}
				change={handleChange}
				callempty={handle_empty}
				ref={searchRef}
			>
				<div className="">
					{list && ObjectKeys(list)?.map(cid=>{
						return (
							<AccountItem 
								data={list[cid]}
								elmId={cid} 
								key={cid}
								selectItem={selectItem}
							/>
						)
					})}
				</div>
			</DropInput>
		</div>
    )   
}

export default SelectDropAccount;




const AccountItem  = ({ data,  selectItem }) => {
	
	let subAcc = getStore("sub_account_group")?.[data.accountId];
	
	

	return (
		<div className="item_acc">
			<div className="_dsplFlx account_item_box account_header"  onClick={()=>selectItem(data?.accountId, null)}>
				<div className="_dsplFlx" >
					<div className="code" >
						{data?.code} 
					</div>
					<div className="name">
						{data?. name}
					</div>
				</div>
			</div>
			{subAcc?.length && 
			<div >
				{ subAcc?.map(subacc=>{
					if(!subacc.subAccount){
						return null
					}
					return(
						<div 
							key={subacc.sub_accountId} 
							className="_dsplFlx account_item_box"  
							style={{marginLeft:"35px"}}
							onClick={()=>selectItem(data?.accountId, subacc.sub_accountId)}
						>
							<div className="_dsplFlx" >
								<div className="code" >
									{subacc.subAccount}
								</div>
								<div className="name">
									{subacc.subAccountName}
								</div>
							</div>
						</div>
					)
				})}
			</div>
			}
		</div>
	)
}





